import React from "react";
import Link from "../../../images/Link.svg";

function Portfolio(props) {
    return (
        <section className="portfolio">
            <article className="portfolio__container">
                <h1 className="portfolio__title"></h1>

                <ul className="portfolio__items">
                    <li className="portfolio__item portfolio__line">
                        <a
                            className="portfolio__link"
                            rel="noreferrer"
                            target="_blank"
                            href="https://github.com/polumnana/how-to-learn"> 
                            <img className="portfolio__img" src={Link} alt="  "></img>
                        </a>
                    </li>
                    <li className="portfolio__item portfolio__line">
                        <a
                            className="portfolio__link"
                            rel="noreferrer"
                            target="_blank"
                            href="https://github.com/polumnana/russian-travel"> 
                            <img className="portfolio__img" src={Link} alt="  "></img>
                        </a>
                    </li>
                    <li className="portfolio__item portfolio__line">
                        <a
                            className="portfolio__link"
                            rel="noreferrer"
                            target="_blank"
                            href="https://github.com/polumnana/react-mesto-api-full"> 
                            <img className="portfolio__img" src={Link} alt="  "></img>
                        </a>
                    </li>
                </ul>
            </article>

        </section >
    );
}

export default Portfolio;